.sectionTitle {
  margin-top: 1rem;
  text-align: center;
  font-family: var(--type-second);
}

.intro {
  display: flex;
  justify-content: space-between;
  background: #fff;
  border-radius: 3rem;
  padding: 1rem 2rem 1rem 2rem;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
}

.intro p {
  align-self: center;
  width: 50%;
  margin-left: 3rem;
  line-height: 1.5rem;
  letter-spacing: 0.05rem;
}

.skills {
  margin-top: 2rem;
  margin-bottom: 2rem;
  background: #fff;
  border-radius: 3rem;
  padding: 1rem 2rem 1rem 2rem;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
}

@media (max-width: 40rem) {
  .intro {
    display: block;
  }

  .intro p {
    width: 100%;
    margin-left: 0;
    margin-bottom: 1rem;
    text-align: center;
  }

  .intro img {
    margin-left: 3.8rem;
  }
}